import React, { lazy } from 'react';

import { PokesTypes } from 'types/index';
import styled from 'styled-components/macro';

const ImageContainer = lazy(() => import('components/ImageContainer/ImageContainer'));

interface RootType {
  bigImage?: PokesTypes;
}

const BigImage: React.FC<RootType> = ({ bigImage }: RootType) => {
  const biImageDefaultValue =
    'data:image/png;base64,';

  const placeholder = () => {
    return <img src={bigImage?.placeholderBase64} alt="" style={{ filter: 'blur(18px)' }} />;
  };

  return (
    <ImageWrapper>
      <ImageContainer url={bigImage?.imageHQ || biImageDefaultValue} cn={'deBlur'} fallback={placeholder} />
    </ImageWrapper>
  );
};

const ImageWrapper = styled.div`
  background: #2196f3;
  border-radius: 4px;
  border: 1px solid black;
  width: 425px;
  height: 430px;
  overflow: hidden;
  box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%), 0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);

  @media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
    width: 100%;
  }

  img {
    width: 100%;
    height: auto;
  }
`;

export default BigImage;
